.sidebar {
    width: 20%;
    height: 100vh;
    z-index: 1000;
    position: relative;
    transition: all 0.5s;
    background-color: #2f2f2f;
    box-shadow: -15px 0 15px -10px #292929;
}

.left_sidebar {
    width: 224px;
    padding: 20px;
    border-radius: 10px;
    height: 100vh;
    position: absolute;
    right: 0;
    font-family: Estedad;
    background-color: #2f2f2f;
}


.head_left_sidebar {
    display: flex;
    align-items: center;
    margin-bottom: 30px;
    font-family: yekan;
    margin-top: 20px;
    padding-right: 10%;
}


.head_left_sidebar img {
    width: 60px;
}


.left_sidebar_title {
    font-weight: bold;
    letter-spacing: 0.02em;
}



.tree_list_left_sidebar {
    list-style: none;
    display: flex;
    align-items: flex-start;
    justify-content: flex-start;
    flex-direction: column;
    padding: 0;
    gap: 5px;
    margin-bottom: 2rem;
}



.tree_item_left_sidebar {
    width: 100%;
    position: relative;
    overflow-x: hidden;
}


.tree_item_left_sidebar a {
    display: block;
    padding: 8px 10px;
    width: 100%;
    color: #ccc;
    border-radius: 4px;
    text-align: start;
}

.tree_item_left_sidebar a.active {
    background-color: #494949;
}

.tree_item_left_sidebar a:hover {
    color: #e85823;
    text-decoration: none;
    background-color: #333333;
}



.tree_left_sidebar {
    overflow-y: auto;
    height: calc(100% - 90px);
}


.tree_left_sidebar_head {
    color: #acacac;
    text-align: start;
    font-size: 18px;
}

.icon_tree_item {
    position: absolute;
    top: 10px;
    font-size: 20px;
    left: 10px;
    transition: 0.5s;
}


.isNone {
    padding: 0 10px;
    margin: 10px 0;
}

.isNone a {
    color: #a5a5a5 !important;
}

.active+svg+.isNone {
    display: block;
}


.notActive+svg+.isNone {
    display: none !important;
}

.active+.icon_tree_item {
    transform: rotate(180deg) !important;
}

.closemenu {
    display: none;
    font-size: 35px;
    position: absolute;
    top: 10px;
    left: -32px;
    background-color: #2f2f2f;
    border-radius: 5px;
    box-shadow: 0 0 15px 5px #00000000;
    cursor: pointer;
}





@media only screen and (max-width:1200px) {
    .sidebar {
        width: 30%;
    }
}


@media only screen and (max-width:992px) {
    .sidebar {
        width: 180px;
        position: absolute;
        box-shadow: 0 0 0 0 #000;
    }

    .sidebar::after {
        content: '';
        position: fixed;
        top: 0;
        left: 0;
        width: 100vw;
        height: 100vh;
        background-color: rgba(0, 0, 0, 0.5);
        z-index: -21;
        display: block;
    }


    .sidebar.active::after {
        display: none;
    }

    .sidebar.active {
        right: -180px;

    }

    .sidebar.active>.left_sidebar {
        box-shadow: 0 0 0 0 #000;
    }

    .closemenu {
        display: block;
    }


    ::-webkit-scrollbar {
        width: 1px;
        height: 2px;
        /* عرض اسکرول بار */
    }

    .tree_left_sidebar {
        padding: 5px;
    }

    .tree_left_sidebar_head {
        font-size: 15px;
        margin-bottom: 5px;
    }

    .tree_item_left_sidebar a {
        font-size: 13px;
    }
}

@media only screen and (max-width:576px) {
    .tree_left_sidebar_head {
        font-size: 14px;
        margin-bottom: 5px;
    }

    .tree_item_left_sidebar a {
        font-size: 12px;
    }
}