:root {
  --button-shadow: 0 3px 15px -3px #252525;
  --button-color: #ffffff;
  --button-hover-shadow: 0 3px 15px -5px #000;
  --button-hover-bg: #353535;
}


@font-face {
  font-family: yekan;
  src: url(./Fonts/Yekan.ttf);
}

@font-face {
  font-family: Estedad;
  src: url(./Fonts/Estedad-Medium.woff2);
}

* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
  text-decoration: none;
}

body {
  background-color: #2f2f2f;
  color: #ccc;
  overflow: hidden;
  font-family: Estedad;
  height: 100vh;
}



.container {
  max-width: 1200px;
  margin: 0 auto;
  height: 100vh;
}

.flex {
  position: relative;
  display: flex;
  justify-content: space-between;
}



::-webkit-scrollbar {
  width: 4px;
  height: 4px;
}

::-webkit-scrollbar-thumb {
  background-color: #888;
  border-radius: 6px;
}

::-webkit-scrollbar-thumb:hover {
  background-color: #555;
}

::-webkit-scrollbar-track {
  background: #212121;
  border-radius: 6px;
}



@media only screen and (max-width: 1200px) {
  .container {
    max-width: 992px;
  }
}


@media only screen and (max-width: 992px) {
  .container {
    max-width: 768px;
    overflow: hidden;
  }
}


@media only screen and (max-width: 768px) {
  .container {
    max-width: 576px;
  }
}


@media only screen and (max-width: 576px) {
  .container {
    max-width: 100%;
    padding: 10px;
  }
}