/* متغیرها */
:root {
    --header-padding-top: 15px;
    --header-padding-right: 10px;
    --header-padding-bottom: 8px;
    --header-top: -50px;
    --header-bg-color: #2f2f2f;
    --header-z-index: 10;
    --header-margin-bottom: 20px;
    --header-icon-size: 30px;
    --menu-icon-size: 35px;
}

/* هدر */
.header {
    padding: var(--header-padding-top) var(--header-padding-right) var(--header-padding-bottom);
    top: var(--header-top);
    left: 0;
    position: sticky;
    width: 100%;
    background-color: var(--header-bg-color);
    z-index: var(--header-z-index);
    display: flex;
    align-items: center;
    justify-content: space-between;
    margin-bottom: var(--header-margin-bottom);
    flex-direction: row-reverse;
}

/* تم‌صفحه */
.themePage {
    font-size: 25px;
    cursor: pointer;
    border-radius: 50%;
    width: var(--header-icon-size);
    height: var(--header-icon-size);
}

/* آیکون منو */
.menuIcon {
    font-size: var(--menu-icon-size);
    cursor: pointer;
    display: none;
}

/* مدیا کوئری */
@media only screen and (max-width:1200px) {
    .header {
        flex-direction: row;
    }

    .menuIcon {
        display: block;
    }
}