/* استفاده از فونت خارجی */
@import url('https://fonts.googleapis.com/css2?family=Josefin+Sans:ital,wght@0,100..700;1,100..700&display=swap');

/* صفحه پیدا نشد */
.not_found_page {
    display: flex;
    align-items: center;
    justify-content: center;
    position: relative;
    flex-direction: column;
    width: 100%;
    padding: 50px;
}

/* باکس ورودی */
.boxWrapper {
    text-align: center;
}

/* عنوان صفحه پیدا نشد */
.title_not_found_page {
    font-family: 'Josefin Sans', sans-serif;
    font-size: 220px;
    color: #2f2f2f;
    text-shadow: 2px 2px 0 #c9c9c9, -2px -2px 0 #c9c9c9;
    letter-spacing: -30px;
    font-weight: 700;
}

.title_not_found_page span {
    text-shadow: 2px 2px 0 #ffab00, -2px -2px 0 #ffab00, 0 0 8px #ff8700;
}

/* اطلاعات صفحه پیدا نشد */
.info_not_found_page {
    letter-spacing: 0.05em;
    width: 450px;
    margin: 0 auto;
}

/* دکمه بازگشت به خانه */
.button_home_not_found_page {
    padding: 10px;
    display: block;
    width: 180px;
    margin: 20px auto;
    border: 2px solid #c9c9c9;
    color: #c9c9c9;
    transition: 0.25s;
}

.button_home_not_found_page:hover {
    border: 2px solid #ffab00;
    color: #ffab00;
}

/* استایل‌های حالت موبایل */
@media only screen and (max-width: 576px) {
    .title_not_found_page {
        letter-spacing: -20px;
        font-size: 150px;
    }

    .info_not_found_page {
        width: 100%;
        padding: 10px;
        text-align: justify;
    }
}