/* متغیرها */
:root {
    --padding-vertical: 50px;
    --padding-horizontal: 30px;
    --button-font-size: 15px;
    --button-padding: 5px;
    --button-border-radius: 8px;

    --button-hover-color: #f38862;
    --close-menu-size: 40px;
}

/* تنظیمات عمومی */
.documentation {
    text-align: start;
    padding: var(--padding-vertical) 0 var(--padding-vertical) var(--padding-horizontal);
    overflow-y: auto;
    width: 880px;
    position: relative;
    margin: 30px 0;
}

.documentation h2 {
    margin-bottom: 15px;
}

.documentation a {
    color: #f38862;
    text-decoration: underline;
}

.documentation h3 {
    margin: 10px 0;
    color: #e6e6e6;
}

.documentation code {
    font-size: var(--button-font-size) !important;
}

.documentation ol,
.documentation ul {
    padding: 0 26px;
}

.buttons {
    margin-top: var(--padding-vertical);
    display: flex;
    align-items: center;
    justify-content: space-between;
    padding: 0 var(--padding-horizontal);
}

.button_addres_web {
    border-radius: var(--button-border-radius);
    font-size: var(--button-font-size);
    background-color: #3d3d3d;
    color: var(--button-color) !important;
    border: 1px solid #636363;
    cursor: pointer;
    display: flex;
    align-items: center;
    justify-content: space-between;
    transition: 0.5s;
    box-shadow: var(--button-shadow);
    text-decoration: none !important;
}

.button_addres_web:hover {
    box-shadow: var(--button-hover-shadow);
    background-color: var(--button-hover-bg);
}

.button_addres_web:hover svg {
    color: var(--button-hover-color);
}

.right {
    padding: var(--button-padding) var(--button-padding) var(--button-padding) 20px;
}

.left {
    padding: var(--button-padding) 20px var(--button-padding) var(--button-padding);
}

.button_addres_web svg {
    font-size: calc(var(--button-font-size) + 6px);
    margin-right: 10px;
}

.arrow_right {
    transform: rotate(180deg);
    margin-right: 0 !important;
    margin-left: 10px;
}

.close_menu {
    z-index: 1000;
    position: fixed;
    top: 20px;
    cursor: pointer;
    font-size: var(--close-menu-size);
    display: none;
}

/* مدیا کوئری‌ها */
@media only screen and (max-width:1200px) {
    .documentation {
        padding-right: 50px;
    }
}

@media only screen and (max-width:992px) {
    .documentation {
        padding: var(--padding-vertical) 20px;
        width: 100% !important;
    }

    .close_menu {
        left: calc((100vw - 768px) / 2 + 20px);
        display: block;
    }
}

@media only screen and (max-width:768px) {
    .close_menu {
        left: calc((100vw - 576px) / 2 + 20px);
        display: block;
    }
}

@media only screen and (max-width: 576px) {
    .documentation {
        padding: 38px 10px;
    }

    .documentation h2 {
        font-size: 25px;
    }

    .documentation p {
        font-size: 14px;
    }

    .documentation h3 {
        font-size: 18px;
    }

    .button_addres_web {
        font-size: 14px;

    }

    .button_addres_web svg {
        font-size: 22px;
    }

    .close_menu {
        left: 30px;
        display: block;
    }
}